import React, { useEffect, useState } from 'react'
import BackendService from "../services/BackendService";
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit';
import paginationFactory from 'react-bootstrap-table2-paginator';
import { Link, useHistory, useParams } from 'react-router-dom';
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'


export default function Dashboard() {
  const params = useParams()
  const [requests, setRequests] = useState([]);
  const [isFetching, setIsFetching] = useState(true);

  const { SearchBar } = Search;
  const columns = [{
    dataField: 'UnitType',
    text: 'Unit Type',
    sort: false
  }, {
    dataField: 'IndexCode',
    text: 'Index Code',
    sort: false
  }, {
    dataField: 'CodeName',
    text: 'Code Name',
    sort: false
  }, {
    dataField: 'MainOrSub',
    text: 'Main Or Sub',
    sort: false
  }, {
    dataField: 'Criterias',
    text: 'Criterias',
    sort: false,
    formatter: (cell, row, rowIndex, formatExtraData) => {
        
      return (
          <div className="criterias">{row.Criterias}</div>
      )
    },
  }, {
    dataField: 'Remarks',
    text: 'Remarks',
    sort: false
  }, {
    dataField: 'Result',
    text: 'Result',
    sort: false,
    formatter: (cell, row, rowIndex, formatExtraData) => {
        
      return (
        row.Result ? (
          <div >Yes</div>
        ) : (
          <div >No</div>
        )
      )
    },
  },{
    dataField: 'Image',
    text: 'Image',
    formatter: (cell, row, rowIndex, formatExtraData) => {
        
      return (
        row.Image ?  (
          <a href={row.Image} target="_blank" ><img alt src={row.Image} /></a>
        ) : ( <a></a> )

      );
    }
  }];
  
  const defaultSorted = [{
    dataField: 'name',
    order: 'desc'
  }];

  const pagination = paginationFactory({
    sizePerPage: 150,
    hideSizePerPage: true,
    prePageText: "Previous",
    nextPageText: "Next",
    withFirstAndLast: false,
    hidePageListOnlyOnePage: false
  });

  useEffect(() => {
    BackendService
      .getInspectionReport({UnitId: parseInt(params.unitId), UCId: parseInt(params.ucId)})
      .then((response) => {
        setIsFetching(false);
        console.log(response.data.data.data);
        setRequests(JSON.parse(response.data.data.data));
        console.log(requests)
      });
  }, []);

    return (
        <div className="container-fluid page-body-wrapper">
        <div className="main-panel">
          <div className="content-wrapper">
            <div className="row">

              <div className="col-lg-12 grid-margin stretch-card">
                <div className="card">
                  <div className="card-body">
                    <h4 className="card-title">Inspection Report</h4>
                    <div className="table-responsive">
                    
                    { isFetching ? (
                        <Skeleton duration={3} height={50} count={5} />
                    ) : (
                    <>
                    <BootstrapTable
                        bootstrap4
                        keyField="id"
                        data={ requests }
                        striped
                        condensed
                        columns={ columns }
                        defaultSorted={ defaultSorted }
                        pagination={ pagination } 
                      />
                    </>
                    ) }
                    </div>
                  </div>
                </div>
              </div>
            </div>
            
          </div>
        </div>
        </div>
    )
}
